import React, {FC, FormEvent, FormEventHandler} from "react";
import {Container, Form, FormControl, Navbar} from "react-bootstrap";
import RadioToggle from "./RadioToggle";

interface iKeyValue {
    key: string,
    value: string
}

interface uiState {
    layouts: iKeyValue[],
    layout: string,
    setLayout:  React.Dispatch<React.SetStateAction<string>>,
    lists: iKeyValue[],
    list: string,
    setList:  React.Dispatch<React.SetStateAction<string>>,
}

const TopLevelNav: FC<uiState> = (props: uiState) => {

    const handleSubmit: FormEventHandler<HTMLFormElement> = (event: FormEvent<HTMLFormElement>) => {
        event.preventDefault()
        window.alert('NOT IMPLEMENTED YET')
    }

    return (

        <Navbar sticky="top" expand="md" bg="dark" className="mb-3 shadow">
            <Container >

                <RadioToggle name={`ViewMode`} options={props.layouts} currentState={props.layout} setState={props.setLayout} />

                <Form className="d-flex" onSubmit={handleSubmit}>
                    <FormControl
                        type="search"
                        placeholder="Search by Title"
                        className="me-2 text-center"
                        aria-label="Search"
                    />
                </Form>

                <RadioToggle name={`ListName`} options={props.lists} currentState={props.list} setState={props.setList} />

            </Container>
        </Navbar>

    );
};

export default TopLevelNav;


